*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html[lang="ar"] body { direction: rtl; text-align: right; }

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app {
  width: 100%;
  max-width: 500px;
  background: white;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  overflow: hidden;
}

/* === НОВЫЕ СТИЛИ ДЛЯ КОМПАКТНОГО HEADER === */

.header {
  background: #4a5568;
  color: white;
  padding: 12px 15px; /* Уменьшаем вертикальные отступы */
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 10px; /* Расстояние между рядами */
}

/* Общий стиль для рядов в header */
.header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px; /* Расстояние между элементами в ряду */
  width: 100%;
}

.app-title {
  font-size: 1.25rem; /* Немного уменьшаем заголовок */
  margin: 0;
  text-align: left;
  flex-shrink: 0; /* Чтобы заголовок не сжимался */
}

/* Контейнер для кнопок режимов */
.view-mode-buttons {
  display: flex;
  gap: 5px;
  justify-content: flex-start; /* Выравниваем по левому краю */
  flex-grow: 1; /* Позволяем занимать доступное место */
  overflow-x: auto; /* Горизонтальный скролл, если не помещается */
  -webkit-overflow-scrolling: touch; /* Плавный скролл на iOS */
  padding-bottom: 5px; /* Небольшой отступ для тени скроллбара */
  min-width: 0;
}

/* Скрываем скроллбар */
.view-mode-buttons::-webkit-scrollbar {
  display: none;
}


/* Контейнер для фильтра */
.filter-container {
  flex-shrink: 0; /* Фильтр не сжимается */
  align-content: center;
}


/* Ряд с дополнительной информацией (статистика и прогресс) */
.sub-info {
  justify-content: space-between;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.8);
  margin-top: 5px;
}
.stats, .progress-indicator {
  background: rgba(255, 255, 255, 0.1);
  padding: 4px 10px;
  border-radius: 12px;
  margin: 0;
}
.stats .accuracy{
    display: inline;
}


/* Убираем старые ненужные стили */
.header-top, .header-controls {
  display: contents; /* Эти обертки больше не нужны для стилизации */
}


/* --- Адаптивность --- */

/* Стили для Desktop (широких экранов) */
@media (min-width: 769px) {
  
  .app-title {
    font-size: 1.5rem;
  }
}

/* Стили для мобильных устройств (узких экранов) */
@media (max-width: 768px) {
  .header {
    padding: 10px;
  }
  .lang-select {
    padding: 4px 8px;
    font-size: 0.8rem;
  }
}

@media (max-width: 480px) {
  .header-row {
      flex-wrap: wrap; /* Разрешаем перенос, если совсем тесно */
  }
  .app-title {
      width: 100%;
      margin-bottom: 5px;
  }
  .view-mode-buttons {
      width: 100%;
      justify-content: center;
  }
}/* Контейнер для выбора языка */
.language-control {
  width: 100%;
  max-width: 300px;
  margin: 0 auto 10px;
}

/* Выпадающий список языка */
.lang-select {
  width: auto; 
  min-width: 60px;
  max-width: 120px;
  padding: 6px 12px;
  font-size: 0.9rem;
  border: 2px solid #e2e8f0;
  border-radius: 20px;
  background: white;
  cursor: pointer;
  outline: none;
  transition: border-color 0.2s ease;
}

.lang-select:focus {
  border-color: #667eea;
}
/* Скрываем стрелку у select на мобильных (опционально) */
.lang-select::-ms-expand,
.lang-select::after {
  display: none;
}


@media (max-width: 768px) {
  .view-mode-buttons {
    gap: 4px;
  }

  .mode-btn {
    font-size: 0.7rem;
    padding: 5px 8px;
  }
}

/* === НОВЫЙ ДИЗАЙН КНОПОК В HEADER === */

.mode-btn {
  /* Форма и шрифт */
  padding: 6px 14px;
  font-size: 0.85rem;
  font-weight: 600;
  border-radius: 18px; /* Более округлая форма */
  white-space: nowrap;
  cursor: pointer;
  flex-shrink: 0;

  /* Стили "ghost button" */
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.4);
  color: rgba(255, 255, 255, 0.9);

  /* Плавный переход для всех состояний */
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Эффект при наведении курсора */
.mode-btn:not(:disabled):hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.8);
  color: #ffffff;
  transform: translateY(-2px); /* Легкий эффект "взлета" */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Стиль для активной/выбранной кнопки */
.mode-btn.active {
  background: #ffffff;
  color: #4a5568; /* Цвет фона хедера для контраста */
  border-color: #ffffff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* Стиль для неактивной кнопки */
.mode-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

/* --- Бонус: Улучшим и стиль фильтра, чтобы он соответствовал --- */
.filter-select {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.4);
  color: rgba(255, 255, 255, 0.9);
  transition: all 0.2s ease;
  padding: 7px 14px;
  font-weight: 600;

  /* Кастомная стрелка для select */
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='%23ffffff' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 1em;
  padding-right: 30px; /* Место для стрелки */
}

.filter-select:hover {
  border-color: rgba(255, 255, 255, 0.8);
  background-color: rgba(255, 255, 255, 0.1);
}


/* Компактный фильтр */
.filter-compact {
  margin: 0 auto;
  max-width: 300px;
}

.filter-select-compact {
  padding: 8px 15px;
  border-radius: 20px;
  border: 2px solid #e2e8f0;
  background: white;
  font-size: 0.9rem;
  cursor: pointer;
  outline: none;
  transition: all 0.3s ease;
  width: 100%;
  color: #2d3748;
}

.filter-select-compact:focus {
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.filter-select-compact:hover {
  border-color: #a0aec0;
}
/* Сообщение об отсутствии слов */
.no-words-message {
  text-align: center;
  padding: 40px;
  color: #4a5568;
}

.no-words-message h3 {
  margin-bottom: 15px;
  color: #2d3748;
}

/* Статистика */
.statistics-main {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.statistics-container {
  background: white;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.stats-overview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
  margin-bottom: 30px;
}

.stat-card {
  background: #f7fafc;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  border-left: 4px solid #4299e1;
}

.stat-card h3 {
  margin: 0 0 10px 0;
  color: #4a5568;
  font-size: 0.9rem;
}

.stat-number {
  font-size: 2rem;
  font-weight: bold;
  color: #2d3748;
}

.stat-number.correct {
  color: #48bb78;
}

/* Секции статистики */
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 10px;
}

.section-header h2 {
  color: #2d3748;
  margin: 0;
}

.mistakes-list, .rules-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.mistake-item {
  background: #fff5f5;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #f56565;
}

.mistake-word {
  font-weight: bold;
  font-size: 1.1rem;
  margin-bottom: 5px;
  color: #2d3748;
}

.mistake-translation {
  color: #718096;
  margin-bottom: 5px;
}

.mistake-count {
  color: #e53e3e;
  font-weight: bold;
  margin-bottom: 5px;
}

.mistake-rule {
  font-size: 0.9rem;
  color: #4a5568;
  font-style: italic;
}

.rule-stat-item {
  background: #f7fafc;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #4299e1;
}
.rule-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.rule-header h4 {
  margin: 0;
  color: #2d3748;
  flex: 1;
}

.accuracy {
  font-weight: bold;
  padding: 4px 8px;
  border-radius: 12px;
  background: #48bb78;
  color: white;
}

.accuracy.low {
  background: #f56565;
}

.rule-details {
  display: flex;
  gap: 15px;
  font-size: 0.9rem;
  color: #718096;
}

/* Кнопки */
.back-btn {
  padding: 8px 16px;
  background: #4a5568;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.back-btn:hover {
  background: #2d3748;
}

.clear-btn {
  padding: 8px 16px;
  background: #e53e3e;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.clear-btn:hover {
  background: #c53030;
}

/* Футер */
.footer {
  padding: 20px;
  text-align: center;
  background: #f7fafc;
  border-top: 1px solid #e2e8f0;
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
}

.reset-btn {
  background: #4a5568;
  padding: 10px 20px;
  font-size: 0.9rem;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.reset-btn:hover {
  background: #2d3748;
}

