.article.der {
  background: linear-gradient(135deg, #f56565, #e53e3e);
}

.article.die {
  background: linear-gradient(135deg, #4299e1, #3182ce);
}

.article.das {
  background: linear-gradient(135deg, #48bb78, #38a169);
}

.word-text {
  font-weight: 600;
  color: #2d3748;
  font-size: 1.1rem;
}

.word-translation {
  color: #718096;
  font-size: 0.95rem;
  flex: 1;
}

.exception-badge {
  background: #f56565;
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.more-words {
  grid-column: 1 / -1;
  text-align: center;
  color: #718096;
  font-style: italic;
  padding: 15px;
  background: #edf2f7;
  border-radius: 8px;
  margin-top: 10px;
}

/* Адаптивность для правил */
@media (max-width: 1024px) {
  .rule-words {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }
}

@media (max-width: 768px) {
  .rules-main {
    padding: 0 15px;
  }
  
  .rule-category {
    padding: 20px;
  }
  
  .category-title {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .rule-words {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  
  .word-example {
    flex-wrap: wrap;
    gap: 8px;
  }
  
  .word-translation {
    flex-basis: 100%;
    margin-left: 55px;
  }
  
  .exception-badge {
    margin-left: auto;
  }
}

@media (max-width: 480px) {
  .rules-list-container {
    padding: 10px 0;
  }
  
  .rules-main {
    padding: 0 10px;
  }
  
  .rule-category {
    padding: 15px;
    border-radius: 12px;
  }
  
  .rule-item {
    padding: 15px;
    margin-bottom: 20px;
  }
  
  .rule-title {
    font-size: 1.1rem;
    margin-bottom: 15px;
  }
  
  .word-example {
    padding: 10px;
  }
  
  .article {
    min-width: 40px;
    padding: 4px 8px;
  }
}

/* Анимации для плавного появления */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.rule-category {
  animation: fadeInUp 0.6s ease-out;
}

.rule-item {
  animation: fadeInUp 0.8s ease-out;
}

.rule-item:nth-child(odd) {
  border-left-color: #667eea;
}

.rule-item:nth-child(even) {
  border-left-color: #764ba2;
}

/* Стили для пустых правил */
.empty-rule {
  text-align: center;
  padding: 40px;
  color: #a0aec0;
  font-style: italic;
  background: #f7fafc;
  border-radius: 8px;
  border: 2px dashed #e2e8f0;
}

/* Скрытие пустых категорий */
.rule-category:has(.empty-rule) {
  display: none;
}


/* Контейнеры выбора языка */
.language-switcher{
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}
.language-select-container {
  display: inline-block;
  justify-content: center;
  margin-bottom: 10px;
}

/* Кнопки языков (десктоп) */
.language-switcher button {
  width: 36px;
  height: 28px;
  font-size: 12px;
  font-weight: 600;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: #f8f9fa;
  color: #495057;
  cursor: pointer;
  transition: all 0.2s ease;
  margin: 0 2px;
}

.language-switcher button.active {
  background: #0d6efd;
  color: white;
  border-color: #0d6efd;
}

/* Выпадающий список (мобильный) */
.lang-select {
  padding: 6px 12px;
  font-size: 0.9rem;
  border: 2px solid #e2e8f0;
  border-radius: 20px;
  background: white;
  cursor: pointer;
  outline: none;
}
